* {
    padding: 0;
    margin: 0;
  }
  body {
    background-color: rgb(212, 152, 228);
  }
  
  .container {
    margin-top: 50px;
    height: 500px;
    border: 2px solid black;
    position: relative;
    border-radius: 20px;
    box-shadow: 5px 5px 5px black;
    background-color: pink;
  }
  
  .ColorPicker {
    text-align: center;
  }
  
  h2 {
    font-size: 24px;
  }
  
  .pick-the-color {
    margin-top: 100px;
    font-size: 18px;
    border: 2px solid black;
    background-color: rgb(41, 221, 41);
  }
  
  .box {
    border: 5px solid black;
    padding: 20px;
    height: 370px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px black;
  }
  
  .color-buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    margin-top: 201px;
  }
  
  .color-button {
    width: 60px;
    height: 60px;
    background-color: #fff;
    border: 2px solid black;
    border-radius: 10px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    transition: transform 0.2s ease-in-out;
  }
  
  .color-button:hover {
    transform: scale(1.1);
  }